	module.exports = {"data":{"markdownRemark":{"html":"<h2>Acceptance Criteria</h2>\n<p>The User Story, as an item in your backlog, is a placeholder for a conversation. In this conversation,\nthe Product Owner and the Delivery Team reach an understanding on the desired outcome.</p>\n<p>The Acceptance Criteria tells the Delivery Team how the code should behave. Avoid writing the <strong>“How”</strong> of the User Story; keep to the <strong>“What”</strong>.\nIf the team is following Test Driven Development (TDD), it may provide the framework for the automated tests.\nThe Acceptance Criteria will be the beginnings of the test plan for the QA team.</p>\n<p>Most importantly, if the story does not meet each of the Acceptance Criteria, then the Product Owner should not be accepting the story at the end of the iteration.</p>\n<h4>More Information:</h4>\n<p>Nomad8 provides an <a href=\"https://nomad8.com/acceptance_criteria/\">FAQ on Acceptance Criteria</a></p>\n<p>Leading Agile on <a href=\"https://www.leadingagile.com/2014/09/acceptance-criteria/\">Acceptance Criteria</a></p>","fields":{"slug":"/agile/acceptance-criteria/"},"frontmatter":{"title":"Acceptance Criteria"}}},"pathContext":{"slug":"/agile/acceptance-criteria/"}}

